<template>
  <v-card class="pa-13 h-100" elevation="0">
    <div class="math_wrap">
      <div
        class="math_item"
        :style="
          `height:${H}px;
          animation-duration: ${time}s;`
        "
      ></div>
    </div>
  </v-card>
</template>

<script>
export default {
  name: "HelloWorld",
  data: () => ({
  }),
  props: {
    H: {
      type: Number,
    },
    
  },
  computed: {
    time() {
      return (Math.PI * Math.sqrt(this.H / 9.81)) / 10;
    }
  },
};
</script>
